// Dashboard
.dashboard__block {
  &--user {
    padding: 40px 20px 35px 20px; }
  &--progress {
    padding: 15px 40px; }
  &--self-asessment {
    padding: 20px 40px;
    .radial-progress {
      margin: 15px auto 15px auto; } }
  &--cities {
    padding: 15px 25px 15px 35px; }
  &--favorite-cities {
    padding: 20px 40px;
    .dashboard__block__heading {
      margin-bottom: 20px; } }
  &--notes {
    padding: 20px 40px; }
  &--compare {
    padding: 20px 40px; }

  &--blogs {
    padding: 20px 40px; } }


.dashboard {
  &__user-avatar {
    display: inline-block;
    border: 2px solid $borderColor;
    border-radius: 50%;
    max-width: 100px;
    max-height: 100px; }
  &__user-name {
    font-size: 18px;
    font-weight: 500;
    margin-top: 15px; }
  &__user-since {
    font-size: 12px;
    color: rgba($fontColor, .7); }
  &__user-settings {
    margin-top: 10px;
    display: inline-block;
    font-size: 12px;
    color: rgba($fontColor, .7); }
  &__block {
    &__heading {
      font-size: 18px;
      font-weight: 500;
      &__tip {
        display: inline-block;
        font-size: 12px;
        line-height: 15px;
        color: rgba($fontColor, .7);
        font-weight: 400;
        position: relative;
        margin-left: 30px;

        .ico {
          position: absolute;
          left: 0;
          top: 50%;
          transform: translateY(-50%); }
        span {
          padding-left: 20px; } }

      span {
        margin-top: 5px;
        display: inline-block;
        vertical-align: middle; } } }
  &__progress-left {
    margin-top: 10px;
    color: rgba($fontColor, .7); }

  &__progress__checkboxes {
    .category__questions__item__checkboxes__item {
      border-bottom: 1px solid $borderColor;
      padding-top: 10px;
      padding-bottom: 10px;
      margin-right: 0;
      width: 100%;
      height: 50px;
      label {
        font-size: 14px;
        font-weight: 400; }
      &:last-child {
        border-bottom: none; } } }

  &__self-asessment__icons {
    .ico {
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      vertical-align: middle;
      width: 20px;
      height: 20px;
      margin-right: 10px; }

    &__row {
      margin-bottom: 10px;
      margin-top: 5px;
      span {
        font-weight: 500; }
      .fl--right {
        font-size: 12px;
        color: rgba($fontColor, .7); } } }
  // Cities
  &__city {

    &__image {
      position: relative;
      border-radius: 6px;
      overflow: hidden;
      img {
        width: 100%; }
      &__name {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 20px;
        font-weight: 500;
        color: white; }
      &__match {
        background: $red;
        border-top-left-radius: 6px;
        border-bottom-left-radius: 6px;
        position: absolute;
        right: 0;
        top: 10px;
        padding: 4px 10px;
        font-size: 12px;
        line-height: 1;
        color: white;
        span {
          font-weight: 500; } }
      &__controls {
        position: absolute;
        bottom: 0px;
        left: 0;
        width: 100%;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
        background: rgba($blue, .7);
        a {
          display: inline-block;
          padding: 8px 15px;
          transition: background .3s ease-in-out;
          &:hover {
            background: rgba(black, .3); } } } }

    &__metrics {
      &__item {
        cursor: pointer;
        width: 100%;
        height: 100%; }
      .ico {
        width: 16px;
        height: 16px;
        background-position: center;
        background-repeat: no-repeat;
        margin-bottom: 3px; } } }

  // Favorite cities
  &__favorite-city {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid $borderColor;
    &__number {
      display: inline-block;
      border-radius: 15px;
      padding: 3px 8px;
      background: $blue;
      line-height: 1;
      font-size: 20px;
      font-weight: 700;
      color: white; }
    &__name {
      display: inline-block;
      font-size: 18px;
      font-weight: 700; }

    &__match {
      display: inline-block;
      background: $red;
      font-size: 12px;
      line-height: 1em;
      height: 20px;
      font-weight: 700;
      border-radius: 6px;
      color: white;
      padding: 5px 10px;
      span {
        font-weight: 400; } }

    &__controls {
      a:last-child .ico {
        margin-right: 0px; }
      .btn {
        margin-right: 20px;
        text-align: center;
        background: white;
        &:hover {
          background: $blue; }
        &:last-child {
          margin-right: 0;
          .ico {
            margin-right: 0; } } }
      .ico {
        width: 21px;
        height: 14px;
        background-repeat: no-repeat;
        background-position: center;
        vertical-align: middle;
        margin-right: 20px;
        transition: margin-bottom .3s ease-in-out;
        &:hover {
          margin-bottom: -5px; } } }

    // Onclick
    &__content {
      overflow: hidden;
      visibility: hidden;
      pointer-events: none;
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      align-items: flex-start;
      padding: 15px 30px 15px 50px;
      transform: translate3d(-100%, 0, 0);
      position: absolute;
      &.active {
        position: relative;
        visibility: visible;
        pointer-events: auto;
        transition: transform .4s ease-in-out;
        transform: translate3d(0, 0, 0); } } }

  &__notes {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: baseline; }
  &__note-compact {
    display: block;
    margin-top: 25px;
    text-align: center;
    border-radius: 5px;
    box-shadow: 0 2px 6px rgba(black, .2);
    padding: 10px 5px;
    flex: 1 1 28%;
    transition: box-shadow .3s ease;
    margin-right: 5%;
    .ico {
      transition: background .4s ease-in-out; }
    &:hover {
      box-shadow: 0 2px 10px rgba(black, .4);
      .ico {
        background-image: url('../img/dash-note-blue.svg'); } }

    &__name {
      font-size: 16px;
      font-weight: 500;
      margin-top: 5px;
      line-height: 1.2em; } }

  &__compare {
    &__heading {
      font-size: 24px;
      font-weight: 500;
      line-height: 1.2em;
      margin-top: 100px;
      margin-bottom: 15px; } }

  &__invite-form {
    margin-top: 15px;
    &__add {
      display: block;
      font-size: 12px;
      color: rgba($fontColor, .7); } }

  // Saved blog posts
  &__blog {
    display: block;
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    transition: box-shadow .3s ease-in-out;
    &--dark-bg {
      &:after {
        display: block;
        content: ' ';
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: 1;
        background: rgba($blue, .5); } }

    &:hover {
      box-shadow: 0 2px 6px rgba(black, .4); }
    &__name {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      z-index: 2;
      display: inline-block;
      text-align: center;
      transform: translate(-50%, -50%);
      font-size: 15px;
      font-weight: 700;
      color: white;
      text-shadow: 0 0 3px rgba(black, .65); }
    &__match {
      position: absolute;
      bottom: 0;
      right: 0;
      z-index: 2;
      background: $red;
      border-top-left-radius: 6px;
      color: white;
      font-size: 12px;
      line-height: 1;
      padding: 5px 10px; } } }


// results

.dash-results, .dash-fav-cities {
  .dashboard__block__heading {
    margin-bottom: 20px; }
  &__wrapper {
    padding: 25px 40px 50px 40px; }

  .dashboard__city__metrics {
    .dashboard__city__metrics__item {
      .ico {
        vertical-align: middle; }
      span {
        margin-left: 10px;
        vertical-align: top; } } }

  &__city {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: flex-start;
    width: 100%;
    padding-top: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid $borderColor;
    &:first-child {
      padding-top: 35px; }
    &:last-child {
      border-bottom: none; }
    &__image {
      position: relative;
      overflow: hidden;
      &:hover {
        .dash-results__city__control {
          left: 0; } } }
    &__name {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 20px;
      font-weight: 500;
      color: white; }
    &__match {
      background: $red;
      border-top-left-radius: 6px;
      border-bottom-left-radius: 6px;
      position: absolute;
      right: 0;
      top: 10px;
      padding: 4px 10px;
      font-size: 12px;
      line-height: 1;
      color: white;
      span {
        font-weight: 500; } }
    &__control {
      position: absolute;
      height: 100%;
      width: 40px;
      left: -40px;
      top: 0;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      background: rgba($blue, .7);
      border-top-left-radius: 6px;
      border-bottom-left-radius: 6px;
      transition: left .3s ease-in-out;
      a {
        flex: 1 1 50%;
        display: inline-block;
        padding: 8px 10px;
        position: relative;
        transition: background .3s ease-in-out;
        &:first-child {
          border-bottom: 1px solid rgba(white, .8); }
        .ico {
          position: absolute;
          top: 50%;
          transform: translateY(-50%); }

        &:hover {
          background: rgba(black, .3); } } }

    &__desc {
      list-style: none;
      li {
        position: relative;
        margin-bottom: 5px;
        &:last-child {
          margin-bottom: 0; }
        &::before {
          background: url('../img/ico-tick-green.svg') no-repeat center;
          width: 15px;
          height: 13px;
          display: inline-block;
          content: ' ';
          margin-right: 10px;
          position: absolute;
          top: 5px;
          left: 0; }
        span {
          display: inline-block;
          padding-left: 30px; } } }


    &__buttons {
      .btn {
        display: block;
        margin-bottom: 13px;
        &:last-child {
          margin-bottom: 0; } } } } }


// Dashboard Favorite cities

.dash-fav-cities {
  // nested above
  .dashboard__favorite-city {
    cursor: pointer;
    transition: background .4s ease-in-out, padding .3s ease-in-out;
    &:hover, &.active {
      background: rgba($borderColor, .3);
      padding-left: 10px;
      padding-right: 10px; }
    &.active {} } }


.dashboard__city-note {
  display: block;
  position: relative;
  width: 130px;
  height: 130px;
  .ico-dash-note-bg {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 1; }
  .ico-dash-note-body {
    position: absolute;
    left: 50%;
    top: 15px;
    transform: translateX(-50%);
    z-index: 2; }
  .ico-dash-note-add {
    position: absolute;
    left: 50%;
    bottom: 26px;
    transform: translateX(-50%);
    z-index: 3;
    transition: box-shadow .2s ease, transform .5s ease-in-out; }
  span {
    text-align: center;
    display: inline-block;
    font-size: 16px;
    position: absolute;
    bottom: 0;
    width: 100%;
    transition: all .4s ease; }
  &:hover {
    span {
      color: $blue;
      font-weight: 700; }
    .ico-dash-note-add {
      transform: translateX(-50%) rotate(180deg);
      box-shadow: -2px -2px 4px rgba(black, .0); }
    .ico-dash-note-add.editable {
      transform: translateX(-50%) rotateY(-180deg); } } }
