// HEADER

.header__navigation {
  display: inline-block;
  padding-top: 10px;
  a {
    padding-right: 20px;
    position: relative;
    @include transition();
    &:last-child {
      padding-right: 0; }
    &:hover {
      color: $blue; }
    &.active {
      color: $blue;
      &::after {
        display: block;
        position: absolute;
        content: ' ';
        bottom: -12px;
        width: 100%;
        height: 2px;
        left: 0;
        background: $blue; } } } }

.header__navigation--main {
  float: right;
  font-size: 16px;
  font-weight: 400;
  a.active {
    font-weight: 700; }
  @include mq($until: tablet) {
    font-size: 14px;
    padding-top: 20px;
    a {
      padding-right: 15px; } }
  @include mq($until: bp660) {
    font-size: 13px;
    a {
      padding-right: 10px; } } }


.header__searchbar {
  @include border();
  @include border-rounded();
  background: rgba($borderColor, .1);
  position: relative;
  height: 40px;
  input[type="text"] {
    width: 100%;
    display: inline-block;
    border: none;
    padding-left: 60px;
    background: transparent;
    height: 38px;
    padding-right: 15px;
    &:focus {
      outline: none;
      border: none; } } }
.header__searchbar__submit {
  @include absolute-y-center();
  left: 20px;
  input[type="submit"] {
    font-size: 0;
    border: none;
    -webkit-appearance: none; }
  &:after {
    display: inline-block;
    background: url('../img/magnifying-glass.svg') no-repeat 0 center;
    width: 20px;
    height: 38px;
    content: ' ';
    cursor: pointer; } }

.header__profile {
  @include border-radius(50%);
  @include border();
  display: inline-block;
  position: relative;
  float: right;
  @include transition();
  width: 60px;
  height: 60px;
  @include mq($until: tablet) {
    width: 45px;
    height: 45px;
    margin-bottom: 10px; }
  @include mq($until: mobile) {
    width: 30px;
    height: 30px;
    position: absolute;
    right: 15px;
    top: -2px;
    .img-fluid {
      max-width: 100%; } }

  &:hover {
    border-color: $blue;
    .header__profile__link {
      color: $blue; } } }

.header__profile__link {
  color: white;
  font-size: 12px;
  @include text-shadow(1px,1px,1px, rgba(black, .5));
  @include absolute-x-center();
  bottom: 10px;
  @include transition(); }

.header__logo {
  @include mq($until: tablet) {
    margin-top: 10px; } }

.header--theme-compact {
  .header__searchbar {
    height: 30px;
    input[type="text"] {
      width: 100%;
      padding-left: 20px;
      height: 28px;
      padding-right: 30px; } }
  .header__searchbar__submit {
    left: auto;
    right: 15px;
    &:after {
      height: 26px;
      vertical-align: -2px; } }
  .header__profile {
    float: none;
    width: 30px;
    height: 30px;
    margin-left: 15px; } }

// FOOTER

.footer__links {
  text-align: justify;
  @include mq($until: mobile) {
    margin-top: 15px; }
  &:after {
    content: ' ';
    display: inline-block;
    width: 100%;
    line-height: 1;
    height: 1px; }
  a {
    font-size: 16px;
    font-weight: 500;
    opacity: .7;
    margin-bottom: 15px;
    display: block;
    @include transition();
    @include mq($until: desktop) {
      font-size: 15px; }
    @include mq($until: tablet) {
      font-size: 14px; }
    &:last-child {
      margin-bottom: 0; }
    &:hover {
      opacity: 1; } } }

.footer__links__block {
  display: inline-block; }

.footer__logo {
  margin-top: 25px; }

// heading

.heading__title {
  font-size: 28px;
  font-weight: 500;
  @include mq($until: desktop) {
    font-size: 24px; } }

.heading__subtitle {
  font-size: 13px;
  opacity: .7;
  max-width: 320px;
  line-height: 1.1em;
  @include mq($until: tablet) {
    max-width: 220px; }
  @include mq($until: mobile) {
    max-width: none;
    margin-bottom: 20px; } }


.progress {
  @include border-radius(50%);
  background: white;
  position: relative;
  display: inline-block;
  @include transition; }
